import styled from 'styled-components';
import { Icon } from '@mui/material';
import contactListLogo from './asset/Logo1.svg';
import { useState, useEffect } from 'react';
import socketIOClient from 'socket.io-client';

import GroupChat from './GroupChat';
import UsernameDialog from './UsernameDialog';

const ENDPOINT = 'http://127.0.0.1:5000';

function App() {
  const [username, setUsername] = useState('');
  const [usernameTemp, setUsernameTemp] = useState('');
  const [isUserNameUnique, setIsUserNameUnique] = useState(true);
  const [socket, setSocket] = useState();
  const [allMsg, setAllMsg] = useState([]);

  useEffect(() => {
    // console.log('connecting to the server using socket');
    const socket = socketIOClient(ENDPOINT);
    socket.on('connect', () =>
      console.log(`connecting ${socket.id} to server`)
    );
    socket.on('connect_error', () => {
      setTimeout(() => socket.connect(), 5000);
    });

    socket.on('newUser', (newUserName) => {
      console.log('#### newUser ####');
      if (newUserName === '') {
        setIsUserNameUnique(false);
      } else {
        setUsername(newUserName);
      }
    });

    socket.on('newMsg', (msg) => {
      console.log('newMsg');
      const newMsg = [
        {
          name: msg.name,
          time: msg.time,
          msg: msg.msg,
        },
      ];

      setAllMsg((prevAllMsg) => [...prevAllMsg, ...newMsg]);
    });

    socket.on('disconnect', () =>
      console.log(`disconnecting ${socket.id} to server`)
    );

    setSocket(socket);
  }, []);

  const handleClose = () => {
    console.log('##### handleClose ####');
    console.log(usernameTemp);
    console.log(username);
    socket.emit('newUser', usernameTemp);
  };

  const handleUsernameChange = (e) => {
    console.log('#### handle user name change ####');
    console.log(e.target.value);
    setUsernameTemp(e.target.value);
  };

  useEffect(() => {
    console.log('usernametemp change');
    console.log(usernameTemp);
  }, [usernameTemp]);

  return (
    <WindowWrapper>
      <UsernameDialog
        username={username}
        isUserNameUnique={isUserNameUnique}
        handleUsernameChange={handleUsernameChange}
        handleClose={handleClose}
        usernameTemp={usernameTemp}
      />
      <MainWrapper>
        <TopDiv>
          <BottomMidDiv>
            <LeftDiv>
              <Icon
                style={{
                  height: '80px',
                  width: '80px',
                  margin: '12px',
                  float: 'right',
                }}
              >
                <img
                  src={contactListLogo}
                  alt='contact list logo'
                  style={{
                    opacity: '90%',
                    filter:
                      'invert(63%) sepia(49%) saturate(7119%) hue-rotate(208deg) brightness(95%) contrast(82%)',
                  }}
                />
              </Icon>
            </LeftDiv>
            <MiddleDiv
              style={{ backgroundColor: 'black', backgroundImage: 'none' }}
            ></MiddleDiv>
            <RightDiv
              style={{
                position: 'relative',
              }}
            >
              <div
                style={{
                  background: '#fff',
                  width: '90px',
                  height: '90px',
                  borderRadius: '50px',
                  display: 'flex',
                  justifyContent: 'center',
                  alignItems: 'center',
                  position: 'absolute',
                  bottom: 0,
                  left: 0,
                }}
              >
                {username}
              </div>
            </RightDiv>
          </BottomMidDiv>
        </TopDiv>
        <BottomDiv>
          <BottomMidDiv>
            <LeftDiv></LeftDiv>
            <MiddleDiv>
              <GroupChat allMsg={allMsg} username={username} socket={socket} />
            </MiddleDiv>
            <RightDiv></RightDiv>
          </BottomMidDiv>
          <BottomLowerDiv></BottomLowerDiv>
        </BottomDiv>
      </MainWrapper>
    </WindowWrapper>
  );
}

export default App;

const WindowWrapper = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100vw;
`;

const MainWrapper = styled.div`
  background-color: #212e52;
  opacity: 40%;
  padding: 5px;
  border-radius: 5px;
  height: 92vh;
  width: 70vw;
  min-height: 920px;
  min-width: 1500px;
  display: flex;
  flex-direction: column;
`;

const TopDiv = styled.div`
  background-color: black;
  height: 105px;
`;

const BottomDiv = styled.div`
  background-color: black;
  flex: 1 1 auto;
  align-items: stretch;
  display: flex;
  flex-direction: column;
`;

const BottomMidDiv = styled.div`
  min-height: 100px;
  height: 85%;
  display: flex;
`;

const BottomLowerDiv = styled.div`
  height: 15%;
`;

const LeftDiv = styled.div`
  width: 110px;
`;

const RightDiv = styled.div`
  width: 110px;
`;

const MiddleDiv = styled.div`
  background-color: #141517;
  background-image: url("data:image/svg+xml,%3Csvg width='100' height='100' viewBox='0 0 100 100' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M11 18c3.866 0 7-3.134 7-7s-3.134-7-7-7-7 3.134-7 7 3.134 7 7 7zm48 25c3.866 0 7-3.134 7-7s-3.134-7-7-7-7 3.134-7 7 3.134 7 7 7zm-43-7c1.657 0 3-1.343 3-3s-1.343-3-3-3-3 1.343-3 3 1.343 3 3 3zm63 31c1.657 0 3-1.343 3-3s-1.343-3-3-3-3 1.343-3 3 1.343 3 3 3zM34 90c1.657 0 3-1.343 3-3s-1.343-3-3-3-3 1.343-3 3 1.343 3 3 3zm56-76c1.657 0 3-1.343 3-3s-1.343-3-3-3-3 1.343-3 3 1.343 3 3 3zM12 86c2.21 0 4-1.79 4-4s-1.79-4-4-4-4 1.79-4 4 1.79 4 4 4zm28-65c2.21 0 4-1.79 4-4s-1.79-4-4-4-4 1.79-4 4 1.79 4 4 4zm23-11c2.76 0 5-2.24 5-5s-2.24-5-5-5-5 2.24-5 5 2.24 5 5 5zm-6 60c2.21 0 4-1.79 4-4s-1.79-4-4-4-4 1.79-4 4 1.79 4 4 4zm29 22c2.76 0 5-2.24 5-5s-2.24-5-5-5-5 2.24-5 5 2.24 5 5 5zM32 63c2.76 0 5-2.24 5-5s-2.24-5-5-5-5 2.24-5 5 2.24 5 5 5zm57-13c2.76 0 5-2.24 5-5s-2.24-5-5-5-5 2.24-5 5 2.24 5 5 5zm-9-21c1.105 0 2-.895 2-2s-.895-2-2-2-2 .895-2 2 .895 2 2 2zM60 91c1.105 0 2-.895 2-2s-.895-2-2-2-2 .895-2 2 .895 2 2 2zM35 41c1.105 0 2-.895 2-2s-.895-2-2-2-2 .895-2 2 .895 2 2 2zM12 60c1.105 0 2-.895 2-2s-.895-2-2-2-2 .895-2 2 .895 2 2 2z' fill='%23272a31' fill-opacity='0.4' fill-rule='evenodd'/%3E%3C/svg%3E");
  flex: 1 1 auto;
  border-radius: 5px;
`;
