import { useEffect, useState } from 'react';

const MessageBubble = ({ username, time, msg, position }) => {
  const [dateAndTime, setDateAndTime] = useState();

  useEffect(() => {
    // console.log(time);
    let d = new Date(time);
    // console.log(d);
    setDateAndTime(d);
  }, []);

  return (
    <div
      style={{
        display: 'flex',
        alignItems: 'flex-start',
        float: position,
        clear: position,
        marginBottom: '15px',
        width: position === 'left' ? '100%' : null,
      }}
    >
      {position === 'left' ? (
        <div
          style={{
            background: '#fff',
            padding: '1rem',
            borderRadius: '40px',
            outline: 'none',
            border: 'none',
            color: 'black',
            fontSize: '12px',
            width: '20px',
            height: '20px',
            margin: '5px',
            display: 'flex',
            justifyContent: 'center',
            alignItems: 'center',
          }}
        >
          {username}
        </div>
      ) : null}
      <div>
        <div
          style={{
            display: 'flex',
            flexDirection: 'row',
            justifyContent: position === 'right' ? 'flex-end' : 'flex-start',
            marginRight: position === 'right' ? '10px' : 'auto',
            marginLeft: position === 'left' ? '10px' : 'auto',
          }}
        >
          <div
            style={{
              display: 'flex',
              justifyContent: 'center',
              alignItems: 'center',
              color: 'grey',
              fontSize: '12px',
            }}
          >
            {dateAndTime
              ? `${dateAndTime.getFullYear()} - ${
                  dateAndTime.getMonth() + 1
                } - ${dateAndTime.getDate()}`
              : ''}
          </div>
        </div>
        <div
          style={{
            background: '#fff',
            padding: '10px',
            display: 'inline-block',
            borderRadius: '10px',
            outline: 'none',
            border: 'none',
            color: 'black',
            fontSize: '20px',
            width: `${msg.length * 12}px`,
            minHeight: '20px',
            maxWidth: '65%',
            float: position === 'right' ? 'right' : 'left',
            marginTop: '5px',
          }}
        >
          {msg}
        </div>
      </div>
      {position === 'right' ? (
        <div
          style={{
            background: '#fff',
            padding: '1rem',
            borderRadius: '40px',
            outline: 'none',
            border: 'none',
            color: 'black',
            fontSize: '12px',
            width: '20px',
            height: '20px',
            margin: '5px',
            display: 'flex',
            justifyContent: 'center',
            alignItems: 'center',
          }}
        >
          {username}
        </div>
      ) : null}
    </div>
  );
};

export default MessageBubble;
