import sendIcon from './asset/sendIcon.svg';
import MessageBubble from './MessageBubble';

import * as Styled from './GroupChatStyled';

const GroupChat = ({ allMsg, username, socket }) => {
  const handleSubmit = (e) => {
    e.preventDefault();
    // console.log('submitted');
    // get the text to be pushed to group window
    // console.log(e.target['input'].value);
    const newMsg = {
      name: username,
      time: Date.now(),
      msg: e.target['input'].value,
    };
    // setAllMsg(allMsg.concat([newMsg]));

    // console.log('emiiting new message');
    socket.emit('newMsg', newMsg);

    // clear the text input section
    e.target['input'].value = '';
  };

  // console.log(`all message length: ${allMsg.length}`);

  return (
    <Styled.MainWrapper>
      <Styled.MessagesWindow>
        <Styled.MessagesAlignBottom>
          {typeof allMsg === 'number'
            ? allMsg
            : allMsg.map((msg) => (
                <div
                  key={`${msg.name} ${msg.time} Math.random()`}
                  style={{ display: 'block' }}
                >
                  <MessageBubble
                    username={msg.name}
                    time={msg.time}
                    msg={msg.msg}
                    position={msg.name === username ? 'right' : 'left'}
                  />
                </div>
              ))}
        </Styled.MessagesAlignBottom>
      </Styled.MessagesWindow>
      <Styled.NewMsgSection onSubmit={handleSubmit}>
        <Styled.EnterMsgLabel>
          <span>
            <svg
              stroke='currentColor'
              fill='none'
              strokeWidth='2'
              viewBox='0 0 24 24'
              strokeLinecap='round'
              strokeLinejoin='round'
              height='1em'
              width='1em'
              xmlns='http://www.w3.org/2000/svg'
            >
              <path d='M12 20h9'></path>
              <path
                id='pencil'
                style={{ transformOrigin: '0% 100%' }}
                d='M16.5 3.5a2.121 2.121 0 0 1 3 3L7 19l-4 1 1-4L16.5 3.5z'
              ></path>
            </svg>
          </span>
          <Styled.TextInput
            id='input'
            type='text'
            placeholder='enter message here...'
            autoComplete='off'
          />
        </Styled.EnterMsgLabel>
        <Styled.MsgSubmitButton type='image' src={sendIcon} alt='alt' />
      </Styled.NewMsgSection>
    </Styled.MainWrapper>
  );
};

export default GroupChat;
