import styled from 'styled-components';

export const MainWrapper = styled.div`
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
`;

export const MessagesWindow = styled.div`
  color: yellow;
  width: 100%;
  height: 100%;
  position: relative;
  overflow-y: auto;
  flex-grow: 1;

  &::-webkit-scrollbar {
    width: 10px;
  }

  &::-webkit-scrollbar-track {
    box-shadow: inset 0 0 5px grey;
    border-radius: 5px;
  }

  &::-webkit-scrollbar-thumb {
    background: darkgrey;
    border-radius: 5px;
  }
`;

export const MessagesAlignBottom = styled.div`
  position: absolute;
  width: 100%;
  height: 100%;
  bottom: 0;
`;

export const NewMsgSection = styled.form`
  opacity: 90%;
  background-color: grey;
  height: 70px;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  padding: 5px;
  margin-top: 10px;
`;

export const EnterMsgLabel = styled.label`
  width: 90%;
  min-width: 240px;
  height: 45%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1em;
  border-radius: 40px;

  background: #fff;
`;

export const TextInput = styled.input`
  width: 100%;
  height: 100%;
  padding: 1em;
  outline: none;
  border: none;

  background: transparent;
  transition: all ease 0.25s;
  font-size: 20px;
`;

export const MsgSubmitButton = styled.input`
  height: 70px;
  width: 70px;
  margin: 12px;
  float: right;
`;
